import { Component, OnInit } from '@angular/core';
import { Reimbursement, defaultReimbursement } from '../interfaces/reimbursement';
import { UserReimbursementsComponent } from '../user-reimbursements/user-reimbursements.component';
import { UserService } from '../shared/user.service';
import { ActivatedRoute, Router } from '@angular/router';
import { ReimbursementsService } from '../shared/reimbursements.service';

@Component({
  selector: 'app-reimbursement',
  templateUrl: './reimbursement.component.html',
  styleUrls: ['./reimbursement.component.css']
})
export class ReimbursementComponent implements OnInit {
  
  reimbursement: Reimbursement = defaultReimbursement;
  constructor(private route: ActivatedRoute, private user: UserService,
              private reimbService: ReimbursementsService, private router: Router) { }

  ngOnInit() {
    this.reimbService.getUserReimbursements(this.user.getUserID()).subscribe(reimbs =>{
      console.log(reimbs.length);
      const reimbID = this.route.snapshot.params['reimbursementId'];
      for (let i = 0; i < reimbs.length; i++){
        if (reimbs[i].id == reimbID){
          this.reimbursement = reimbs[i];
          break;
        }
      }
    });
  }
  
  delete(){
    let leavePage: any = false;
    this.reimbService.deleteReimbursement(this.reimbursement.id).subscribe(leavePage = result => result);
    console.log(leavePage);
    if (leavePage) {
      console.log('heh');
      this.router.navigate(['reimbursement']);
    }
  }
  
  logout() {
    this.user.logout();
  }

}



