import { Component, AfterViewInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { ReimbursementsService } from '../shared/reimbursements.service';
import { UserService } from '../shared/user.service';
import { Reimbursement } from '../interfaces/reimbursement';

@Component({
  selector: 'app-assigned-reimbursements',
  templateUrl: './assigned-reimbursements.component.html',
  styleUrls: ['./assigned-reimbursements.component.css']
})
export class AssignedReimbursementsComponent implements AfterViewInit {
  
  reimbursements: Reimbursement[] = [];
  readonly ROOT_URL = 'http://localhost:8082/TRMSProject/reimbursements/assigned';
  constructor(private reimbService: ReimbursementsService, private user: UserService, private http: HttpClient) { }
  
  ngAfterViewInit() {
    this.http.post<Reimbursement[]>(this.ROOT_URL, this.user.person).subscribe(reimbursements => this.reimbursements = reimbursements);
  }

  logout() {
    this.user.logout();  
  }
}



