import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { CommonModule} from '@angular/common';
import { HttpClientModule} from '@angular/common/http';
import { RouterModule, Routes } from '@angular/router';

import { AppComponent } from './app.component';
import { HeaderComponent } from './header/header.component';
import { FooterComponent } from './footer/footer.component';
import { LoginFormComponent } from './login-form/login-form.component';
import { UserReimbursementsComponent } from './user-reimbursements/user-reimbursements.component';
import { AddReimbursementComponent } from './add-reimbursement/add-reimbursement.component';

import { UserService } from './shared/user.service';
import { ReimbursementsService } from './shared/reimbursements.service';

import { LoginGuard } from './shared/login.guard';
import { EmployeeTypeGuard } from './shared/employee-type.guard';
import { AssignedReimbursementsComponent } from './assigned-reimbursements/assigned-reimbursements.component';
import { ReimbursementComponent } from './reimbursement/reimbursement.component';
import { AssignedReimbursementComponent } from './assigned-reimbursement/assigned-reimbursement.component';

const appRoutes: Routes = [
  {
    path: '',
    component: LoginFormComponent
  },
  {
    path: 'reimbursement',
    canActivate: [LoginGuard],
    component: UserReimbursementsComponent,
  },
  {
    path: 'add',
    canActivate: [LoginGuard],
    component: AddReimbursementComponent
  },
  {
    path: 'assigned',
    canActivate: [LoginGuard, EmployeeTypeGuard],
    component: AssignedReimbursementsComponent
  },
  {
    path: 'reimbursement/:reimbursementId',
    canActivate: [LoginGuard],
    component: ReimbursementComponent
  },
  {
    path: 'assigned/:reimbursementId',
    canActivate: [LoginGuard],
    component: AssignedReimbursementComponent
  }
];

@NgModule({
  declarations: [
    AppComponent,
    HeaderComponent,
    FooterComponent,
    LoginFormComponent,
    UserReimbursementsComponent,
    AddReimbursementComponent,
    AssignedReimbursementsComponent,
    ReimbursementComponent,
    AssignedReimbursementComponent
  ],
  imports: [
    CommonModule,
    BrowserModule,
    RouterModule.forRoot(appRoutes),
    HttpClientModule
  ],
  providers: [UserService, ReimbursementsService, LoginGuard, EmployeeTypeGuard, UserReimbursementsComponent],
  bootstrap: [AppComponent]
})
export class AppModule { }



