import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { HttpModule } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import { Person, DefaultPerson } from '../interfaces/person';
import { Router } from '@angular/router';
import 'rxjs/add/operator/map';

@Injectable()
export class UserService {
  
  readonly ROOT_URL = 'http://localhost:8082/TRMSProject/';
  person: Person = DefaultPerson;
  
  constructor(private http: HttpClient, private router: Router) { }

  getUser(em: string, password: string) {
    const _properties: {[name: string]: string} = {['em']: em, ['pass']: password};
    const headers = new HttpHeaders(_properties);
    this.http.get<Person>(this.ROOT_URL + 'login', { headers }).subscribe(person => { this.person = person; } );
  }
  
  setPerson(person) {
    this.person = person;
  }
  
  getUserID() {
    return this.person.id;
  }
  
  getSupervisorID() {
    return this.person.supervisorId;
  }
  
  getHeadID() {
    return this.person.headId;
  }
  
  getEmployeeType() {
    return this.person.employeeTypeId;
  }
  
  logout() {
    this.person = DefaultPerson;
    this.router.navigate(['']);
  }
}



