import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Router } from '@angular/router';
import { UserService } from '../shared/user.service';
import { Reimbursement, defaultReimbursement } from '../interfaces/reimbursement';

@Component({
  selector: 'app-add-reimbursement',
  templateUrl: './add-reimbursement.component.html',
  styleUrls: ['./add-reimbursement.component.css']
})
export class AddReimbursementComponent {
  
  reimbursement: Reimbursement = defaultReimbursement;
  
  readonly ROOT_URL = 'http://localhost:8082/TRMSProject/reimbursements/add';
  
  constructor(private http: HttpClient, private user: UserService, private router: Router) { }
  
  addReimbursement(event) {
    event.preventDefault();
    this.constructReimbursement(event);
    this.http.post<any>(this.ROOT_URL, this.reimbursement).subscribe(res => {if (res) {this.router.navigate(['reimbursement']); }});
  }
    
  constructReimbursement(event) {
    this.reimbursement.employeeId = this.user.getUserID();
    this.reimbursement.supervisorId = this.user.getSupervisorID();
    this.reimbursement.headId = this.user.getHeadID();
    this.reimbursement.eventId = event.target.elements[0].value;
    this.reimbursement.description = event.target.elements[1].value;
    this.reimbursement.justification = event.target.elements[2].value;
  }
  
  testForID() {
    console.log(this.user.getUserID());
    console.log(this.user.getSupervisorID());
    console.log(this.user.getHeadID());
  }
  
  testReimbursement() {
    console.log(this.reimbursement.id);
  }
  
  logout() {
    this.user.logout();
  }
}



