import { Injectable } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';
import { Observable } from 'rxjs/Observable';
import { UserService } from '../shared/user.service';
import { Reimbursement } from '../interfaces/reimbursement';
import { Person } from '../interfaces/person';

@Injectable()
export class ReimbursementsService {
  
  total: any = 0;
  readonly ROOT_URL = 'http://localhost:8082/TRMSProject/reimbursements';
  constructor(private http: HttpClient, private user: UserService) { }
  
  getUserReimbursements(userID: number): Observable<Reimbursement[]> {
    return this.http.get<Reimbursement[]>(this.ROOT_URL + `/${userID}`);
  }
  
  deleteReimbursement(reimbursementID: number): any {
    return this.http.get<boolean>(this.ROOT_URL + `/delete/${reimbursementID}`);
  }
  
  getTotalReimbursements(person: Person) {
    return this.http.get<number>(this.ROOT_URL + `/total/${this.user.getUserID()}`);
  }
  
  updateReimbursement(reimbursementID: number) {
    return this.http.get(this.ROOT_URL + `/update/${reimbursementID}`);
  }
  
  denyReimbursement(reimbursementID: number) {
    return this.http.get(this.ROOT_URL + `/deny/${reimbursementID}`);
  }
}



