import { Component, OnInit, AfterViewInit } from '@angular/core';
import { Router } from '@angular/router';

import { Reimbursement, defaultReimbursement } from '../interfaces/reimbursement';
import { ReimbursementsService } from '../shared/reimbursements.service';
import { UserService } from '../shared/user.service';

@Component({
  selector: 'app-user-reimbursements',
  templateUrl: './user-reimbursements.component.html',
  styleUrls: [  './user-reimbursements.component.css'
             ]
})
export class UserReimbursementsComponent implements AfterViewInit {

  reimbursements: Reimbursement[] = [];
  moneys = 0;
  constructor(private reimbService: ReimbursementsService, private userService: UserService, private router: Router) { }
  
  ngAfterViewInit() {
    const userID = this.userService.getUserID();
    this.reimbService.getUserReimbursements(userID).subscribe(reimbursements => { this.reimbursements = reimbursements; });
    this.reimbService.getTotalReimbursements(this.userService.person).subscribe(money => {this.moneys = money; console.log(this.moneys); });
  }
  
  getReimbursements() {
    const loggedUserID = this.userService.getUserID();
    this.reimbService.getUserReimbursements(loggedUserID).subscribe(reimbursements => { this.reimbursements = reimbursements; });
  }
  
  returnReimbursements(): Reimbursement[] {
    return this.reimbursements;
  }
   
   logout() {
     this.userService.logout();
   }
}



