import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { UserService } from '../shared/user.service';
import { ReimbursementsService } from '../shared/reimbursements.service';
import { Reimbursement, defaultReimbursement } from '../interfaces/reimbursement';
import { HttpClient } from '@angular/common/http';
@Component({
  selector: 'app-assigned-reimbursement',
  templateUrl: './assigned-reimbursement.component.html',
  styleUrls: ['./assigned-reimbursement.component.css']
})
export class AssignedReimbursementComponent implements OnInit {

  reimbursement: Reimbursement = defaultReimbursement;
  readonly ROOT_URL = 'http://localhost:8082/TRMSProject/reimbursements/assigned';
  constructor(private route: ActivatedRoute, private user: UserService,
              private reimbService: ReimbursementsService, private router: Router, private http: HttpClient) { }

  ngOnInit() {
    this.http.post<Reimbursement[]>(this.ROOT_URL, this.user.person).subscribe(reimbursements => {
      const reimbID = this.route.snapshot.params['reimbursementId'];
      for (let i = 0; i < reimbursements.length; i++) {
        if (reimbursements[i].id == reimbID) {
          this.reimbursement = reimbursements[i];
        }
      }
    });
  }
  
  
  
  update() {
    this.reimbService.updateReimbursement(this.reimbursement.id).subscribe();
    this.router.navigate(['reimbursement']);
  }
  
  deny() {
    this.reimbService.denyReimbursement(this.reimbursement.id).subscribe();
    this.router.navigate(['reimbursement']);
  }
  
  logout() {
    this.user.logout();
  }
}



